.container {
  // breakpoints:
  // mobile - 480px
  // tablet - 768px
  // desktop - 1200px

  // mobile style
  max-width: 480px;
  padding: 0 15px;
  margin: 0 auto;

  // tablet style
  @media screen and (min-width: 768px) {
    max-width: 768px;
  }

  // desktop style
  @media screen and (min-width: 1200px) {
    max-width: 1200px;
  }
}
